<template>
  <div class="nav">
    <img class="info" src="./images/bg_green.png" />
    <p class="adr">崇山路校区图书馆</p>
    <h6 class="time">{{period}}</h6>
    <p class="tip">{{date}}</p>
    <p class="nam">{{name}}</p>
  </div>
</template>

<script>
const time = require("../../utils/time");
export default {
  name: "Nav",
  data() {
    return {
      date:time.timestampToTime(Date.now(), 'Y-M-D'),
      name:'',
      period:''
    };
  },
  mounted(){
    let {period,nam}=this.$route.query
    this.period=period
    this.name=nam
  }
};
</script>

<style lang="less" scoped>
.nav {
  width: 90vw;
  position: relative;
  margin: 0 auto;
  .info {
    width: 100%;
  }
  .adr {
    position: absolute;
    left: 4%;
    top: -6%;
    font-size: 1.2em;
    color: #fff;
  }
  .time {
    position: absolute;
    top: 8%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    width: 100%;
    text-align: center;
    font-size: 1.8em;
  }
  .tip {
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    font-size: 1em;
  }
  .nam {
    position: absolute;
    bottom: 1%;
    right: 3%;
    color: #fff;
    font-size: 1em;
  }
}
</style>