<template>
  <div class="cont">
    <img src="./images/suc_icon.png" alt="" />
    <h1>签到成功</h1>
    <p class="time">{{time}}</p>
  </div>
</template>

<script>
const time = require("../../utils/time");
let timer=null
export default {
  name: "Cont",
  data(){
      return {
          time:time.timestampToTime(Date.now(), 'h:m:s')
      }
  },
  mounted(){
      timer=setInterval(()=>{
          this.time=time.timestampToTime(Date.now(), 'h:m:s')
      },1000)
  },
  destroyed(){
      clearInterval(timer)
      timer=null
  }
};
</script>

<style lang="less" scoped>
.cont {
  width: 90vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 27%;
  h1 {
    margin-top: 40px;
    margin-bottom: 0;
  }
  img {
    width: 22%;
  }
  .time {
    color:#69D796;
    font-size:1.5em;
  }
}
</style>