<template>
  <div>
    <transition-group name="box" appear class="choose">
      <div class="sel" key="1">
        时间段：
        <select ref="sel">
          <option value="07:30 - 12:00">07:30 - 12:00</option>
          <option value="12:00 - 17:00">12:00 - 17:00</option>
          <option value="17:00 - 21:30">17:00 - 21:30</option>
        </select>
      </div>
      <div key="2">姓名：<input type="text" v-model="nam" /></div>
      <button key="3" @click="goHome">确定</button>
    </transition-group>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nam: localStorage.getItem("name") || "",
    };
  },

  methods: {
    goHome() {
      let period = this.$refs.sel.value;
      let { nam } = this;
      this.$router.push({ name: "Home", query: { period, nam } });
    },
  },
  destroyed() {
    localStorage.setItem("name", this.nam);
  },
};
</script>

<style lang="less" scoped>
.choose {
  margin: 30vw auto;
  width: 90vw;
  height: 80vw;
  box-shadow: gray 5px 5px 5px;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  button {
    width: 50%;
    border-radius: 15px;
    height: 10%;
    background: #fff;
  }
  .sel {
    position: absolute;
    left: 19%;
  }
  input {
    padding: 10px;
    border-radius: 10px;
    outline: none;
  }
  .box-enter {
    transform: scale(0);
  }
  .box-enter-to {
    transform: scale(1);
  }
  .box-enter-active {
    transition: all 0.8s ease;
  }
}
</style>